import { RepositoryService } from './../../shared/repository.service';
import { Component, OnInit, AfterViewInit, ViewChild, Inject } from '@angular/core';
import { MatTableDataSource, MatSort, MatPaginator } from '@angular/material';
import { Business } from '../../_interface/business.model';
import { ErrorHandlerService } from '../../shared/error-handler.service';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { MatDialog } from '@angular/material';
import { SuccessDialogComponent } from '../../shared/dialogs/success-dialog/success-dialog.component';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent implements OnInit, AfterViewInit {

  public displayedColumns = ['person_name', 'business_name', 'business_gst_number', 'action'];
  public dataSource = new MatTableDataSource<Business>();
  private dialogConfig;
  public burl = '';

  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;

  constructor(private http: HttpClient, private location: Location, private dialog: MatDialog,
    private repoService: RepositoryService, private errorService: ErrorHandlerService, private router: Router,
    @Inject('BASE_URL') baseUrl: string) {
    this.burl = baseUrl;
  }

  ngOnInit() {
    this.getAllOwners();
  }

  ngAfterViewInit(): void {
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
  }

  public getAllOwners = () => {
    this.http.get(this.burl + 'business')
      .subscribe(res => {
        this.dataSource.data = res as Business[];
      },
        (error) => {
          this.errorService.handleError(error);
        })
  }

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }


  public redirectToUpdate = (id: string) => {
    let url: string = `business/edit/${id}`;
    this.router.navigate([url]);
  }

  public redirectToDelete = (id: string, indx: number) => {

    let Apiurl: string = this.burl + `business/${id}`;

    if (window.confirm('Are you sure')) {
      const data = this.dataSource.data;
      data.splice((this.paginator.pageIndex * this.paginator.pageSize) + indx, 1);
      this.dataSource.data = data;
      this.http.delete(Apiurl)
        .subscribe(res => {
          let dialogRef = this.dialog.open(SuccessDialogComponent, this.dialogConfig);

          //we are subscribing on the [mat-dialog-close] attribute as soon as we click on the dialog button
          dialogRef.afterClosed()
            .subscribe(result => {
              this.router.navigate(['/list']);;
            });
        },
          (error => {
            this.errorService.dialogConfig = { ...this.dialogConfig };
            this.errorService.handleError(error);
          })
        )


    }


  }

}
