import { RepositoryService } from './../../shared/repository.service';
import { Component, OnInit, Inject } from '@angular/core';
import { FormControl, FormGroup, Validators, NgForm } from '@angular/forms';
import { Location } from '@angular/common';
import { Business } from '../../_interface/business.model';
import { MatDialog } from '@angular/material';
import { SuccessDialogComponent } from '../../shared/dialogs/success-dialog/success-dialog.component';
import { ErrorHandlerService } from '../../shared/error-handler.service';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {

  public businessForm: FormGroup;
  public item: Business;
  public id: string = '';
  public burl: string = '';

  private dialogConfig;

  constructor(private location: Location, private repository: RepositoryService,
    private dialog: MatDialog, private errorService: ErrorHandlerService,
    private router: Router, private route: ActivatedRoute, private http: HttpClient,
    private errorHandler: ErrorHandlerService, @Inject('BASE_URL') baseUrl: string) {
    this.burl = baseUrl;
  }

  ngOnInit() {


    this.businessForm = new FormGroup({
      person_name: new FormControl('', [Validators.required, Validators.maxLength(50)]),
      business_name: new FormControl('', [Validators.required, Validators.maxLength(100)]),
      business_gst_number: new FormControl('', [Validators.required, Validators.maxLength(20)])
    });

    this.getOwnerDetails();

    this.dialogConfig = {
      height: '300px',
      width: '400px',
      disableClose: true,
      data: {}
    }
  }

  public hasError = (controlName: string, errorName: string) => {
    return this.businessForm.controls[controlName].hasError(errorName);
  }

  public onCancel = () => {
    this.location.back();
  }

  private getOwnerDetails = () => {
    this.id = this.route.snapshot.params['id'];

    let apiUrl: string = this.burl + `business/${this.id}`;

    this.http.get(apiUrl)
      .subscribe((res: Business) => {
        this.item = res;
        this.businessForm.setValue({
          person_name: this.item.person_name,
          business_name: this.item.business_name,
          business_gst_number: this.item.business_gst_number
        });

      },
        (error) => {
          this.errorHandler.handleError(error);
        })
  }

  public updateBusiness = (ownerFormValue) => {
    if (this.businessForm.valid) {
      this.executeOwnerUpdate(ownerFormValue);
    }
  }

  private executeOwnerUpdate = (ownerFormValue) => {
    let owner: any = {
      id: this.id,
      person_name: ownerFormValue.person_name,
      business_name: ownerFormValue.business_name,
      business_gst_number: Number(ownerFormValue.business_gst_number)
    }

    let apiUrl: string = `https://localhost:44309/business/${this.id}`;
    this.http.put(apiUrl, owner)
      .subscribe(res => {
        let dialogRef = this.dialog.open(SuccessDialogComponent, this.dialogConfig);

        //we are subscribing on the [mat-dialog-close] attribute as soon as we click on the dialog button
        dialogRef.afterClosed()
          .subscribe(result => {
            this.location.back();
          });
      },
        (error => {
          this.errorService.dialogConfig = { ...this.dialogConfig };
          this.errorService.handleError(error);
        })
      )
  }
  onFormSubmit(form) {
    if (this.businessForm.valid) {
      this.executeOwnerUpdate(form);
    }
  }

}
