import { RepositoryService } from './../../shared/repository.service';
import { Component, OnInit, Inject } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Location } from '@angular/common';
import { OwnerForCreation } from '../../_interface/ownerForCreation.model';
import { MatDialog } from '@angular/material';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { SuccessDialogComponent } from '../../shared/dialogs/success-dialog/success-dialog.component';
import { ErrorHandlerService } from '../../shared/error-handler.service';

@Component({
  selector: 'create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.css']
})
export class CreateComponent implements OnInit {
  public ownerForm: FormGroup;
  private dialogConfig;
  public burl: string = '';

  constructor(private location: Location, private http: HttpClient, private dialog: MatDialog,
    private errorService: ErrorHandlerService, @Inject('BASE_URL') baseUrl: string) {

    this.burl = baseUrl;

  }

  ngOnInit() {
    this.ownerForm = new FormGroup({
      person_name: new FormControl('', [Validators.required, Validators.maxLength(50)]),
      business_name: new FormControl('', [Validators.required, Validators.maxLength(100)]),
      business_gst_number: new FormControl('', [Validators.required, Validators.maxLength(20)])
    });

    this.dialogConfig = {
      height: '300px',
      width: '400px',
      disableClose: true,
      data: {}
    }
  }

  public hasError = (controlName: string, errorName: string) => {
    return this.ownerForm.controls[controlName].hasError(errorName);
  }

  public onCancel = () => {
    this.location.back();
  }

  public createOwner = (ownerFormValue) => {
    if (this.ownerForm.valid) {
      this.executeOwnerCreation(ownerFormValue);
    }
  }

  private executeOwnerCreation = (ownerFormValue) => {
    let owner: OwnerForCreation = {
      person_name: ownerFormValue.person_name,
      business_name: ownerFormValue.business_name,
      business_gst_number: Number(ownerFormValue.business_gst_number)
    }

    let apiUrl = this.burl + 'business';
    this.http.post(apiUrl, owner)
      .subscribe(res => {
        let dialogRef = this.dialog.open(SuccessDialogComponent, this.dialogConfig);

        //we are subscribing on the [mat-dialog-close] attribute as soon as we click on the dialog button
        dialogRef.afterClosed()
          .subscribe(result => {
            this.location.back();
          });
      },
        (error => {
          this.errorService.dialogConfig = { ...this.dialogConfig };
          this.errorService.handleError(error);
        })
      )
  }

}
